body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

  .row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 2%;
  }
  
  /* Create four equal columns that sits next to each other */
  .column {
    -webkit-flex: 20% 1;
            flex: 20% 1;
    max-width: 20%;
    padding: 1%;
    box-sizing: border-box;
  }
  
  .imgSearch{    
    border-radius: 5%;
    width:100%;
    margin-top: 18px;
    vertical-align: middle;
  }
  
  @media (max-width: 800px) {
    .column {
      -webkit-flex: 50% 1;
              flex: 50% 1;
      max-width: 50%;
    }
  }
  
  @media (max-width: 600px) {
    .column {
      -webkit-flex: 100% 1;
              flex: 100% 1;
      max-width: 100%;
    }
  }
.section{
    width: 100%;
    height: 60px;
    background-color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    color:#958E8E;
    font-weight: bold;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.image {
    max-width: 40px;
    max-height: 40px;
    position: absolute;
    left: 0;
    margin:0.8% 1%;
}

.inputBox{
    width: 50%;
    height: 40px;
    background: #EFEFEF;
    border: 1px solid #EFEFEF;
    margin-left: 5%;
    margin-top: 0.8%;
    border-radius: 5px;
    
}

.lupa{
    max-width: 20px;
    max-height: 20px;
    margin: 1% 1.5%;
    color: #EFEFEF;
}

.input{
    min-width: 600px;
    padding: 0.7%;
    position: absolute;
    background-color: #EFEFEF; 
    border: none;
    font-weight: bolder;
    border-radius: 5%;
    font-size: 16px;

}
.iconBox{
    position: absolute;
    right: 0;
    width: 20%;
    margin-right: 2%;
    margin-top: 0.8%;
    text-align: right;
}
.iconos{    
    max-width: 30px;
    max-height: 30px;
    padding: 1%;
    margin-right: 5%;
}

.buttonBox{
    position: absolute;
    right: 0;
    width: 20%;
    margin-right: 20%;
    margin-top: 1.5%;
    text-align: right;
}
.buttonText{
    padding: 1%;
    margin-right: 10%;
}

.nameText{  
    position: absolute;
    right: 0;
    width: 20%;
    margin-right: 15%;
    margin-top: 1.5%;
    text-align: right;
}

.user{
    padding: 1%;
    margin-right: 2%;
}

.letter{
    background-color: #958E8E;
    border-radius: 50%;
    padding:  2% 3%;
    font-size: 12px;
    font-weight: bolder;
    color: #EFEFEF;
}
.divAfuera{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 60px;
    left: 0;
    z-index: 100;
